<template>
  <section class="location">
    <div class="container">
      <div class="map-holder">
        <svg width="100%" height="100%" viewBox="0 0 500 500"></svg>
      </div>
      <div class="label-caption">Working Worldwide</div>
      <span class="gimme-drinks">currently available for drinks in Paris</span>
    </div>
  </section>
</template>

<script>
import * as d3 from 'd3'
import * as topojson from 'topojson'
import { TimelineMax } from 'gsap'
import ScrollMagic from 'scrollmagic'
import 'imports-loader?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap'

export default {
  props: ['controller'],
  mounted () {
    var width = 500
    var height = 500
    var rotate = [-4.75, -39.25]
    var Paris = [2.4, 47.3838312]

    var svg = d3.select(this.$el.querySelector('svg'))
    var projection = d3
      .geoOrthographic()
      .scale(250)
      .translate([width / 2, height / 2])
      .clipAngle(90)
      .precision(1)
      .rotate(rotate)

    var path = d3.geoPath().projection(projection)
    svg
      .append('defs')
      .append('path')
      .datum({ type: 'Sphere' })
      .attr('id', 'sphere')
      .attr('d', path)
    svg.append('use').attr('class', 'stroke').attr('xlink:href', '#sphere')
    svg.append('use').attr('class', 'fill').attr('xlink:href', '#sphere')
    var featured
    var points

    var redraw = function () {
      featured.attr('d', d => {
        return path(d)
      })
      points
        .attr('cx', d => {
          return projection(d)[0]
        })
        .attr('cy', d => {
          return projection(d)[1]
        })
        .attr('opacity', d => {
          var geoangle = d3.geoDistance(rotate, [
            projection.rotate()[0],
            projection.rotate()[1]
          ])

          if (geoangle > 1.5707963267949) {
            return '0'
          } else {
            return '1.0'
          }
        })
    }

    var m0
    var o0
    var drag = d3
      .drag()
      .on('start', () => {
        var proj = projection.rotate()
        m0 = [d3.event.sourceEvent.pageX, d3.event.sourceEvent.pageY]
        o0 = [-proj[0], -proj[1]]
      })
      .on('drag', () => {
        var m1 = [d3.event.sourceEvent.pageX, d3.event.sourceEvent.pageY]
        var o1 = [o0[0] + (m0[0] - m1[0]) / 4, o0[1] + (m1[1] - m0[1]) / 4]
        projection.rotate([-o1[0], -o1[1]])
        redraw()
      })
    svg.call(drag)

    d3.json('/static/topojson/world-110m.json', (error, world) => {
      if (error) throw error

      featured = svg
        .insert('path', '.graticule')
        .datum(topojson.feature(world, world.objects.land))
        .attr('class', 'land')
        .attr('d', path)

      points = svg
        .selectAll('circle')
        .data([Paris])
        .enter()
        .append('circle')
        .attr('id', 'paris')
        .attr('cx', d => {
          return projection(d)[0]
        })
        .attr('cy', d => {
          return projection(d)[1]
        })
        .attr('r', '4px')

      var rotation = {
        x: rotate[0],
        y: rotate[1]
      }
      var tl = new TimelineMax()
      tl
        .from(
          this.$el.querySelector('.map-holder'),
          4,
          { alpha: 0, scale: 0.75 },
          'a'
        )
        .from(
        rotation,
        10,
        {
          x: -270,
          y: 0,
          onUpdate: () => {
            projection.rotate([rotation.x, rotation.y])
            redraw()
          }
        },
        'a'
        )
      new ScrollMagic.Scene({
        duration: 400,
        triggerHook: 0.75,
        triggerElement: this.$el
      })
        .setTween(tl)
        .addTo(this.controller)
    })
  }
}
</script>

<style lang="scss">
.location
{
  .container {
    margin-top: 7.5%;
    margin-bottom: 7.5%;
    padding-bottom: 100px;
    text-align: center;
    color: white;
    &:before,
    &:after {
      content: " ";
      display: table;
      clear: both;
    }
  }
  .map-holder {
    width: 500px;
    height: 500px;
    max-width: 100%;
    transform: translateZ(0);
    margin: auto;
  }
  .label-caption {
    margin-top: 1em;
    font-size: 36px;
    font-style: italic;
    font-weight: 100;
    line-height: 1.3em;
  }
  .gimme-drinks {
    letter-spacing: .075em;
    line-height: 1.3em;
    font-family: 'Crimson Text', serif;
  }
  svg {
    cursor: move;
  }
  #sphere {
    stroke: none;
    // fill: #80ffe5;
    // fill: rgb(0, 229, 255);
    // fill: #222;
    fill: none;
  }
  #paris {
    // fill: white;
    fill: rgb(128, 255, 229);
    transition: opacity 1s ease;
    &:hover {
      fill: red;
    }
  }
  .stroke {
    fill: none;
    stroke: #000;
    stroke-width: 3px;
  }
  .fill {
    fill: #fff;
  }
  .graticule {
    fill: none;
    stroke: #fff;
    stroke-width: 0.5px;
    stroke-opacity: 0.1;
  }
  .land {
    // fill: #222;
    fill: #fff;
  }
  .boundary {
    fill: none;
    stroke: #fff;
    stroke-width: 0.5px;
  }
}
</style>



