<template>
  <section class="showcase">
		<div class="container">
      <h2>Picked projects.</h2>
    </div>
    <div class="container bottom-line">
      <a target="_blank" :href="project.link ? project.link : null" class="project-box" v-for="project in projects">
        <div class="gabarit">
          <div class="decoration-box">
            <div class="image-holder" :style="[project.image && {backgroundImage: 'url(' + project.image + ')'}]"></div>
            <div class="decoration-gabarit">
              <div class="outdated-box" v-if="project.outdated">
                <p class="outdated-caption">Outdated link</p>
              </div>
            </div>
          </div>
          <div class="description-box">
            <p>{{project.client}}</p>
            <h3>{{project.name}}</h3>
          </div>
          <span class="date-caption">{{project.date}}</span>
        </div>
      </a>
      <div class="thumb-projects-container">
        <p class="thumb-title">dead list</p>
        <a target="_blank" :href="project.link ? project.link : null" class="thumb-project-box" v-for="project in thumbProjects">
          <span class="date-caption">{{project.date}}</span>
          <h3>{{project.name}}</h3>
          <p>{{project.client}}</p>
        </a>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data () {
    return {
      projects: [
        {
          name: 'Evian Fruits and Plants',
          client: 'Web app',
          date: 'Mar 2017',
          link: 'https://www.evianfruitsetplantes.com/en/en/',
          image: '/static/projects/screenshot_evian.png'
        },
        {
          name: 'Dom Pérignon',
          client: 'Integration',
          date: 'Dec 2016',
          link: 'https://www.domperignon.com/',
          image: '/static/projects/screenshot_domperignon.png'
        },
        {
          name: 'LV Made to order',
          client: 'Web app',
          date: 'Dec 2014',
          link: 'https://eu.louisvuitton.com/eng-e1/stories/made-to-order',
          image: '/static/projects/screenshot_made_to_order.jpg'
        }
      ],
      thumbProjects: [
        {
          name: 'Joop Fragrances',
          client: 'Web app',
          date: 'Jan 2017',
          link: 'https://www.cssdesignawards.com/sites/joop-fragrances/31474/',
          image: '/static/projects/screenshot_joop.png'
        },
        {
          name: 'LV Capucines',
          client: 'Web app',
          date: 'Apr 2015',
          link: 'http://eu.louisvuitton.com/eng-e1/stories/newcapucines',
          image: '/static/projects/screenshot_capucines.png'
          // outdated: true
        },
        {
          name: 'LV Haute Joaillerie',
          client: 'Web app',
          date: 'Dec 2014',
          link: 'http://fr.louisvuitton.com/fra-fr/histoires/haute-joaillerie',
          image: '/static/projects/screenshot_dentelles.png'
          // outdated: true
        }
      ]
    }
  }
}
