<style lang="scss" scoped>
.showcase {
  min-height: 100vh;
  .container {
    padding-top: 40px;
    &.bottom-line:after {
      content: ' ';
      display: block;
      padding-top: 1.2em;
      border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    }
  }
  h2 {
    border-top: 1px solid rgba(255, 255, 255, 0.05);
    padding-bottom: 0.75em;
  }
  .project-box {
    max-width: 415px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
    text-decoration: none;
    @media (min-width: 768px) {
      width: 40%;
      &:nth-of-type(n + 2) {
        margin-top: -30%;
      }
    }
    color: white;
    margin-bottom: 40px;
    .decoration-box {
      padding-bottom: 132%;
      position: relative;
      .image-holder {
        width: 100%;
        height: 100%;
        position: absolute;
        background-size: cover;
        // background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
        background-image: linear-gradient(-225deg, #fffeff 0%, #d7fffe 100%);
        background-position: 50% 50%;
        left: 0;
        top: 0;
        z-index: 0;
      }
      .decoration-gabarit {
        border: 10px solid rgba(255, 255, 255, 0.15);
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        box-sizing: border-box;
        transition: border-width 0.3s ease, border-color 0.3s ease;
      }
      .outdated-box {
        width: 100%;
        height: 100%;
        // background-color: rgba(255, 0, 0, 0.15);
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;
        transition: opacity 0.3s ease;

        p {
          font-size: 26px;
        }
      }
    }
    .gabarit {
      position: relative;
    }
    .date-caption {
      position: absolute;
      top: 0;
      right: 100%;
      white-space: nowrap;
      transform: rotate(-90deg);
      transform-origin: 87% 100%;
      font-size: 12px;
    }
    p {
      font-style: italic;
      font-size: 14px;
      margin: 1em 0 0.75em;
    }
    h3 {
      font-family: 'Abril Fatface', cursive;
      font-size: 1.875em;
    }

    @media (min-width: 768px) {
      &:nth-of-type(odd) {
        margin-right: auto;
        margin-left: 0;

        .date-caption {
          right: auto;
          left: 100%;
          transform: rotate(-90deg);
          transform-origin: 59% 217%;
        }
      }
      &:nth-of-type(even) {
        margin-left: auto;
        margin-right: 0;
        .image-holder {
          left: auto;
          right: 0;
        }
        .description-box {
          text-align: right;
        }
      }
    }
    &:hover {
      .decoration-box {
        .decoration-gabarit {
          opacity: 1;
          border-width: 1px;
          border-color: rgba(255, 255, 255, 1);
          // background-color: rgba(255, 0, 0, 0.15);
        }

        .outdated-box {
          opacity: 1;
        }
      }
    }
  }
  .thumb-projects-container {
    margin-top: 4em;
    .thumb-title {
      color: rgba(255,255,255,.25);
      font-size: 12px;
      line-height: 2em;
    }
    a {
      display: block;
      color: white;
      text-decoration: none;
      line-height: 2em;
      * {
        display: inline;
      }

      .date-caption {
        white-space: nowrap;
        font-size: 10px;
      }
      p {
        font-style: italic;
        font-size: 12px;
        margin: 1em 0 0.75em;
      }
      h3 {
        font-family: 'Abril Fatface', cursive;
        font-size: .875em;
      }
    }
  }
}
</style>



