  <template>
    	<div class="nav-bar" @click="toggleMenu()">
    		<button class="hamburger" :class="{active: $parent.modalState}">
    			<span class="layer bread-top">
    			  <span class="thumb"></span>
          </span>
    			<span class="layer steak">
  			     <span class="thumb"></span>
          </span>
    			<span class="layer bread-bottom">
            <span class="thumb"></span>
          </span>
    		</button>
    	</div>
  </template>

  <script>
  export default {
    methods: {
      toggleMenu () {
        this.$parent.modalState = !this.$parent.modalState
      }
    }
  }
  </script>

  <style lang="scss" scoped>

.nav-bar
{
	position: fixed;
	top: 0;
	left: 0;
	height: 0;
	z-index: 2;
  	@media (min-width: 768px) {
  		top: 50%;
  		margin-top: -35px;
  	}
	.hamburger
	{
		-webkit-appearance: none;
		background-color: transparent;
		border: none;
		box-shadow: none;
		border-radius: 0;
		position: relative;
		cursor: pointer;
		outline: none;

	    // background-color: rgba(0,0,0,.1);
	    padding: 0;
	    margin: 20px;
	    width: 30px;
	    height: 30px;
		.layer
		{
			width: 20px;
    		height: 2px;
			display: block;
			transform-origin: center;
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    margin-left: -15px;
        overflow: hidden;

        transition: all .3s ease;
			&.bread-top
			{
				transform: translateY(-7px);
				width: 15px;
    			margin-left: -10px;
			}
			&.steak
			{
				transform: translateY(-1px);
			}
			&.bread-bottom
			{
        transform: translateY(5px);
        width: 10px;
        margin-left: -5px;
			}
      .thumb {
        transition: background-color .3s ease;
        width: 100%;
        height: 100%;
        display: block;
        background-color: #ffffff;
      }
		}

		&:hover
		{
			.layer
			{
        .thumb {
          background-color: #80ffe5;
        }
			}
		}
		&.active
		{
			.layer
			{
				&.bread-top
				{
					transform: translateY(-1px) rotate(-45deg);
					width: 20px;
    				margin-left: -15px;
				}
				&.steak
				{
					transform: translateY(-1px) translateX(-50%);
					opacity: 0;
				}
				&.bread-bottom
				{
					transform: translateY(-1px) rotate(45deg);
					width: 20px;
    				margin-left: -15px;
				}
        .thumb {
          background-color: #80ffe5;
        }
			}
		}
	}
}
  </style>



