<template>
  <section class="skills">
    <div class="container">
      <!-- <h2>Skills</h2> -->
      <div class="gabarit">
        <div class="skill-box" v-for="skill in skills">
          <h3 class="skill-type">{{skill.title}}</h3>
          <div class="caption-holder">
            <span class="skill-caption" v-for="content in skill.content">{{content}}</span>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data () {
    return {
      skills: [
        {
          title: 'JS Frameworks',
          content: ['VueJS', 'ReactNative', 'React']
        },
        {
          title: 'JS Libraries',
          content: ['GSAP', 'd3js', 'ScrollMagic', 'Velocity']
        },
        {
          title: 'Canvas and WebGL',
          content: ['ThreeJS', 'PIXI', 'PaperJS']
        },
        {
          title: 'CSS Preprocessors',
          content: ['SCSS', 'LESS']
        },
        {
          title: 'Automation',
          content: ['Grunt', 'Gulp', 'Webpack']
        },
        {
          title: 'Backend',
          content: ['Node', 'Firebase']
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.skills {
  color: #fff;
  	h2
  	{
    	padding-top: 100px;
    	padding-bottom: 100px;
  		font-family: 'Abril Fatface', cursive;
  		font-size: 60px;
    	padding-top: 1.2em;
    	padding-bottom: 1.2em;
  	}
    .gabarit {
      margin-bottom: 7.5%;
    	@media (min-width: 768px) {
        text-align: center;
      }
      &:after {
        content: " ";
        display: table;
        clear: both;
      }
    }
    .skill-box {
      width: 300px;
      display: inline-block;
      vertical-align: top;
      margin-bottom: 2em;
    }
    .skill-type {
      font-weight: 200;
      font-style: italic;
      display: inline-block;
      border-bottom: 1px solid rgba(255,255,255,.15);
      padding-bottom: .3em;
    }
    .caption-holder {
      margin-top: 17px;
    }
    .skill-caption {
      font-size: 16px;
      display: block;
      line-height: 1.4;
    }
}
</style>



